.articles-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding: 20px;
  min-height: 100vh;
  background-color: #000;
  overflow: auto;
}

.article {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90%;
  text-align: center;
  padding-bottom: 1rem;
}

.title {
  font-size: 2rem;
  font-weight: 100;
  font-family: 'Russo One', sans-serif;
  color: #8b1313;
}

.text {
  font-size: 1.5rem;
  margin-top: 0.5rem;
  width: 80%;
  color: #fff;
}

.disclaimer {
  font-size: 1rem;
}

.article-images {
  display: flex;
  flex-direction: column;
  max-width: 95vw;
}

.article-image {
  max-width: 25rem;
  padding: 0.5rem;
}

.list {
  text-align: left;
  font-weight: bolder;
  margin-bottom: 2rem;
}

.list-text {
  color: #fff;
}

.shortcut {
  text-decoration: none;
  font-family: 'Russo One', sans-serif;
  color: #8b1313;

  span{
    font-family: 'Russo One', sans-serif;
    color: #fff;
  }
}